import React from 'react';
import {Card, Table, Button} from 'antd';
const dataSource = [
  {
    key: '1',
    name: '',
    age: 32,
    address: '1',
  },
  {
    key: '2',
    name: '',
    age: 42,
    address: '1',
  },
];

const columns = [
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    dataIndex: 'age',
    key: 'age',
  },
  {
    title: '2',
    dataIndex: 'address',
    key: 'address',
  },
];

export default function index(){
  return (
    React.createElement(Card, {
      title: '',
    }, React.createElement(Button, {
      type: 'primary'
    }, 'Primary Button'), React.createElement(Table, {
      columns: columns,
      dataSource: dataSource
    }))
  )
  // return (
  //   <Card>
  //     <Table dataSource={dataSource} columns={columns} />
  //   </Card>
  // )
}

function test(){
  
  const cp = {
    "component": "Card",
    "title": "",
    "children": [{
      "component": "Button",
      "type": 'primary',
      "children": 'Primary Button'
    }, {
      "component": "Table",
      "columns": [
        {
          "title": "",
          "dataIndex": "name",
          "key": "name"
        },
        {
          "title": "",
          "dataIndex": "age",
          "key": "age"
        },
        {
          "title": "",
          "dataIndex": "address",
          "key": "address"
        }
      ]
    }]
  }

  React.createElement(Card, {
    title: '',
  }, React.createElement(Button, {
    type: 'primary'
  }, 'Primary Button'), React.createElement(Table, {
    columns: columns,
    dataSource: dataSource
  }))

}